<template>
	<transition name='mainBody'>
	<div class="content" v-if="showPopup">
		<div class="skillImg">
			<img :src="skillItem[$store.state.dataLogic.skillIndex].image">
		</div>
		<div class="content-padded">
			<p style="text-indent: 1.5rem">{{skillItem[$store.state.dataLogic.skillIndex].Info}}</p>
		</div> 
		<div class="skill-btn">
			<p><a href="javascript:void(0)" class="button button-fill" @click='back'>关闭</a></p>
		</div>
	</div>
	</transition>
</template>

<script>
	import skillOther from '../json/skillOther.js'
	export default{
		data(){
			return {
				skillItem:[],
				showPopup:false
			}
		},
		methods:{
			back(){
				this.$router.go(-1);
			}
		},
		mounted(){
			var _this=this;
			_this.skillItem=skillOther;
			setTimeout(function(){
				_this.showPopup=true;
			},1000);
		}
	}

</script>

<style>
</style>



