<template>
  <div class="homeShow">
    <div class="showImgMe" @click='stopRotate'>
     <img :src="imgMe">
   </div>
   <div class="show-content" :class="{beginRotate:beginState}">
    <div class="show-content-A" @click="skillOpen(0)">
      {{skillItem[0]}}
    </div>
    <div class="show-content-B">
      <ul class="clearfix">
        <li @click="skillOpen(1)">
          {{skillItem[1]}}
        </li>
        <li class="show-content-span">
          <span class="span-release"></span>
          <span class="span-across"></span>
        </li>
        <li class="show-content-li" @click="skillOpen(2)">
          {{skillItem[2]}}
        </li>
      </ul>
    </div>
    <div class="show-content-A" @click="skillOpen(3)">
      {{skillItem[3]}}
    </div>
  </div>
</div>
</template>

<script>
  export default{
    data(){
      return {
        skillItem:['技能','经历','自我评价','兴趣爱好'],
        imgMe:require('../assets/img/me2.png'),
        beginState:false
      }
    },
    methods:{
      stopRotate(){
        this.beginState=!this.beginState;
      },
      skillOpen(index){
        this.$store.state.dataLogic.skillIndex=index;
        this.$router.push({path:'/skillInfo'});
      }
    }
  }
</script>


